.n-wrapper {
    height: 50px;
    display: flex;
    justify-content: space-between;
}

.n-left {
    flex: 1;
    align-items: center;
    display: flex;
    gap: 2rem;
}

.n-name {
    font-size: 1.3rem;
    font-weight: 400;
}

.n-right {
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}

.n-list {
    flex: 10;
}

.n-list>ul {
    display: flex;
    gap: 2rem;
    list-style-type: none;
    margin-right: 4rem;
}

.n-list>ul>li:hover {
    cursor: pointer;
    color: var(--orange);
}

.n-button {
    flex: 2;
}


@media screen and (max-width: 480px) {
    .n-wrapper {
        margin-bottom: -4rem;
    }

    .n-list {
        display: none;
    }

    .n-button {
        display: none;
    }
}