.t-wrapper{
    padding: 0 3rem 0 3rem;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
    flex-direction: column;
    gap: 2rem;
    position: relative;
}

.t-heading{
    align-self: start;
}

.t-heading>*{
    font-size: 2rem;
    font-weight: bold;
}

.t-heading span:nth-of-type(2){
    color: var(--orange);
}

.t-blur1{
    left: 38rem;
    top: 16rem;
}

.t-blur2{
    top: 8rem;
    left: 0;
}

.testimonial{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    padding: 2rem;
    width: 30rem;
    height: 12rem;
    background: rgba(255, 255, 255, 0.26);
    border: 7px solid var(--blueCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
}

.testimonial>img{
    width: 6rem;
    height: 6rem;
    border-radius: 100%;
    object-fit: cover;
}

.testimonial>span{
    text-align: center;
    color: var(--gray);
}

.t-wrapper .swiper{
    width: 100%;
    height: 60%;
}

.t-wrapper .swiper-slide{
    display: flex;
    align-items: center;
    justify-content: center;
}

.t-wrapper .swiper-pagination-bullet-active{
    background: var(--orange);
}

@media screen and (max-width: 480px) {
  .t-wrapper{
    padding: 0;
    margin-top: -3rem;
  }

  .testimonial>img{
    width: 180px;
    height: 180px;
    border-radius: 50%;
    object-fit: cover;
}

.testimonial{
    padding: 0px 10px;
    width: 100%;
    height: 400px;
}
}